@use '../utils/' as *;

.text {
	line-height: 28px;
	letter-spacing: 0.02em;

	&-xs {
		font-size: rem(12);
	}

	&-sm {
		font-size: rem(14);
	}
}

.title {
	font-family: var(--secondary-font);
}

.font-size {
	&--md {
		font-size: rem(24);
	}
	&--lg {
		font-size: rem(40);
	}
}
